.h_login {
  margin-left: 3%;
  font-size: 260%;
  font-weight: bold;
  font-family: Times;
  line-height: 40%;
  color: grey;
  cursor: pointer;
}

.h_login:hover{
  color:black;
}

.h_slash {
  margin-left: 1%;
  font-size: 260%;
  font-weight: bold;
  font-family: Times;
  line-height: 2vw;
  color: grey;
  cursor: pointer;
}

.h_register {
  /* margin-left: %; */
  font-size: 260%;
  font-weight: bold;
  font-family: Times;
  line-height: 20%;
  color: black;
}

.a{
  color:grey;
}

.a:hover{
  color: black
}




.Input {
    border-color: #f0f3f7;
    display: inline-block;
    width: 410px;
    height: 40px;
    border-top-color: white;
    font-size: 16px;
    font-weight:70px;
    color: black;
    font-variant:normal;
    border-width: 1px;


}
.email{
  position : relative;
  right: 185px;
}
.password{
  position : relative;
   right: 175px;
}
@media (max-width: 600px) {
  .Input{
    width: 301px;
  }
  .h_register{
    font-size: 200%;
  }
  .h_login{
    font-size: 200%;
  }
  .email{
    right: 135px;
  }
  .password{
    right: 120px;
  }

}

.Input:hover{
  background-color: #E8E8E8;
  border-color: grey;

}
